import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { PerfilPopover } from 'src/app/perfil/perfil.component';

@Component({
  selector: 'app-revision',
  templateUrl: './revision.page.html',
  styleUrls: ['./revision.page.scss'],
})
export class RevisionPage implements OnInit {

  constructor(public popoverController: PopoverController) { }

  ngOnInit() {
  }

  
  async popoverPerfil(ev: any) {
    const popover = await this.popoverController.create({
      component: PerfilPopover,
      event: ev,
      translucent: true
    });
    return await popover.present();
  }

}
